
@media only screen and (min-width: 1224px) {

html{
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
    }
    
header{
        width: 100%;
        height: 8.5%;
        background-color:rgb(2, 0, 54);
        display: flex;
        align-items: center;
        position: fixed;
    }
    
header img{
        padding: 10px;
    /*     width:max-content; */
        height: 60px;
        width: 140px;
        padding-left: 50px
    }
    
.menu{
        width: 100%;
        display: inline-flex;
        list-style-type: none;
        justify-content: flex-end;
        padding-right: 50px;
      
    }
    
.menu li{
        color: white;
        margin: 0 15px 0 15px;
    }
    
a {
        float: left;
        display: block;
        color: #f2f2f2;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 17px;
        font-family: "Alata";
    
      }
      
      a:hover {
        background: #ffffff;
        color: rgb(25, 0, 114);
        border-radius: 10px;
      }
  }
  
  
*{
    margin: 0;
    padding: 0;
}

@media only screen and (max-width: 1224px) {

    
header{
    width: 100%;
    height: 8.5%;
    background-color:rgb(2, 0, 54);
    display: flex;
    align-items: center;
    position: fixed;
}

header img{
    padding: 10px;
/*     width:max-content; */
    height: 50px;
    width: 140px;
    padding-left: 40px;
    
}

.menu{

    width: 100%;
    display: inline-flex;
    list-style-type: none;
    justify-content: flex-end;
    padding-right: 30px;
}

.menu li{
    color: white;
    margin: 2px;
    font-size: 9px;
}

a {        
    padding: 14px 16px;
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    font-family: "Alata";

  }
  
  a:hover {
    background: #ffffff;
    color: rgb(25, 0, 114);
    border-radius: 10px;
  }


 
}


@media only screen and (max-width: 650px) {



    header{
        width: 100%;
        height: 8.5%;
        background-color:rgb(2, 0, 54);
        display: flex;
        align-items: center;
        position: fixed;
        
    }
    
    header img{
        padding: 10px;
        padding-left: 20px;
    /*     width:max-content; */
        height: 30px;
        width: 85px;
        
    }
    
    .menu{
        width: 100%;
        display: inline-flex;
        list-style-type: none;
        justify-content: flex-end;
        padding-right: 10px;
    }
    
    .menu li{
        
        color: white;
        font-size: 11px;
    
    }
    
    a {
    
        float: left;
        display: block;
        color: #f2f2f2;
        text-align: center;
        text-decoration: none;
        font-size: 8px;
        font-family: "Alata";
    
      }
      
      a:hover {
       font-size: 9px;
        background: rgb(2, 0, 54);
        color: rgb(239, 239, 241);
        border-radius: 10px;
      }
      

}



