@media only screen and (min-width: 1224px) {

    .footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 10%;
        background-color: rgb(2, 0, 54);
        color: rgb(2, 0, 54);
        text-align: center;
      }

    

      .footer-icons{

        width: 30%;
        margin-top: 30px;       
  
        }
  
        .icon-wrapper {
         
          left: 0px;
        }
        
        i {
          padding: 0px 10px;
          
        }
        
        .icon-wrapper i:nth-child(1) {
          color: #4867AA;
          cursor: pointer;
          text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
          transition: all ease-in-out 150ms;
        }
        
        .icon-wrapper i:nth-child(1):hover {
          margin-top: -10px;
          text-shadow: 0px 16px 10px rgba(0, 108, 248, 0.651);
          transform: translate(0, -8);
        }
        
        .icon-wrapper i:nth-child(2) {
          color: #1DA1F2;
          cursor: pointer;
          text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
          transition: all ease-in-out 150ms;
        }
        
        .icon-wrapper i:nth-child(2):hover {
          margin-top: -10px;
          text-shadow: 0px 16px 10px rgba(0, 129, 250, 0.671);
          transform: translate(0, -8);
        }
        
    
        .icon-wrapper i:nth-child(3) {
          color: #fffc00;
          cursor: pointer;
          text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
          transition: all ease-in-out 150ms;
        }
        .icon-wrapper i:nth-child(3):hover {
          margin-top: -10px;
          text-shadow: 0px 16px 10px rgba(175, 164, 0, 0.3);
          transform: translate(0, -8);
        }
  

     

}



